import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';

export function asyncGetMyJourneyListAction(userId)
{
    return function(dispatch, getState)
    {
        dispatch(_getMineListRequestAction());
        return _fetchMineList(userId).then((resp)=>{
            dispatch(_getMineListSuccessAction(resp ? resp.results : []));
        }).catch((error)=>{
            dispatch(_getMineListFailureAction(error));
        });
    };
}

export function asyncGetRecommendJourneyListAction(userId)
{
    return function(dispatch, getState)
    {
        dispatch(_getRecommendListRequestAction());
        return _fetchRecommendList(userId).then((resp)=>{
            dispatch(_getRecommendListSuccessAction(resp ? resp.results : []));
        }).catch((error)=>{
            dispatch(_getRecommendListFailureAction(error));
        });
    };
}

export function goBackAction()
{
    return {
        type: IConstant.Actions.JOURNEY.INDEX.GO_BACK,
    };
}

export function setScrollerPositionAction(offset)
{
    return {
        type: IConstant.Actions.JOURNEY.INDEX.SET_SCROLL_OFFSET,
        scrollViewOffset: offset
    };
}

function _getMineListRequestAction()
{
    return {
        type: IConstant.Actions.JOURNEY.INDEX.GET_MINE_LIST_REQUEST,
    };
}

function _getMineListSuccessAction(resp)
{
    return {
        type: IConstant.Actions.JOURNEY.INDEX.GET_MINE_LIST_SUCCESS,
        dataList: resp,
    };
}

function _getMineListFailureAction(error)
{
    return {
        type: IConstant.Actions.JOURNEY.INDEX.GET_MINE_LIST_FAILURE,
        message: error
    };
}

function _fetchMineList(userId)
{
    let kvDict = {
        pageIndex: 1,
        pageSize: 4,
        userID: userId
    };
    return HttpClient.getFromPortal(IConstant.PortalApis.JOURNEY.GetTourPlanInfo, kvDict)
        .then((resp) => resp.json());
}

function _getRecommendListRequestAction()
{
    return {
        type: IConstant.Actions.JOURNEY.INDEX.GET_RECOMMEND_LIST_REQUEST,
    };
}

function _getRecommendListFailureAction(error)
{
    return {
        type: IConstant.Actions.JOURNEY.INDEX.GET_RECOMMEND_LIST_FAILURE,
        message: error
    };
}

function _getRecommendListSuccessAction(resp)
{
    return {
        type: IConstant.Actions.JOURNEY.INDEX.GET_RECOMMEND_LIST_SUCCESS,
        dataList: resp,
    };
}

function _fetchRecommendList(userId)
{
    let kvDict = {
        pageIndex: 1,
        pageSize: 10000
    };
    return HttpClient.getFromPortal(IConstant.PortalApis.JOURNEY.GetRecommendTourForHome, kvDict)
        .then((resp) => resp.json());
}

export function _setRefreshAction(isRefresh){
    return {
        type: IConstant.Actions.JOURNEY.INDEX.SET_LIST_REFRESH,
        isRefresh
    };
}


