import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, DatePicker, List, ActivityIndicator, Modal } from 'antd-mobile';
import Content from '../../views/common/Content';
import SysUtil from '../../core/utils/SysUtil';
import Config from '../../core/utils/Config';
import '../../core/utils/DateUtil';
import HttpClient from '../../core/utils/HttpClient';
import * as IConstant from '../../core/utils/IConstant';
import {
    asyncGetCreateAction,
    asyncGetMDTypesAction,
    updateMDTypesAction,
    goBackAction,
} from '../../store/actions/journey/createAction.js';
import {_setRefreshAction} from '../../store/actions/journey/indexAction';

import '../../assets/css/journey.css';

class CreateJourneyScreen extends PureComponent {
    constructor(props) {
        super(props);

        this.onChangeName=this.onChangeName.bind(this);
        this.state = {
            date: new Date(),
            selectArray: [],//选中的不容错过集合
            isLoading: false,
            day: 1,
            planName:''
        };
    }
    componentDidMount() {
        if (!this.props.dataList) {
            this.props.dispatch(asyncGetCreateAction());
        }
        if (!this.props.mdTypes) {
            this.props.dispatch(asyncGetMDTypesAction());
        }
        
    }
    render() {
        return (
            <div className="createJourneyBg">
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction());
                    }}>
                    创建行程
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}}>
                    {
                        this.renderPage()
                    }
                    {
                        this.props.isFetching || this.state.isLoading
                            ?
                            <ActivityIndicator
                                toast
                                text="Loading..." />
                            : null
                    }
                </Content>
            </div>
        );
    }
    renderPage() {
        if (!this.props.dataList) {
            return;
        }
        let boxBgImg = require('../../assets/images/journey/bg_step.jpg');
        return (
            <div className='createJourneyBox' style={{ backgroundImage: `url(${boxBgImg})` }}>
                {
                    this.renderCreate()
                }
                {
                    this.renderTheme()
                }
                {
                    this.renderNotMiss()
                }
                {
                    this.renderCommit()
                }
            </div>
        );
    }
    renderCreate() {
        let titImg = require('../../assets/images/journey/bg_step_tit.png');
        let icon = require('../../assets/images/journey/bg_step_top.png');

        return (
            <div className='createJourneyCreate'>
                <div className='createJourneyCreateTit' style={{ backgroundImage: `url(${titImg})` }}>
                    01. 创建行程
                </div>
                <div className='createJourneyCreateIcon' style={{ backgroundImage: `url(${icon})` }}>
                </div>
                <dl>
                    <dt>行程名称：</dt>
                    <dd>
                        <input type="text" className="createJourneyCreateName" placeholder="给行程起个响亮的名字" value={this.state.planName}
                            onChange={this.onChangeName}/>
                    </dd>
                </dl>
                <dl>
                    <dt><span>*</span>旅行天数：</dt>
                    <dd>
                        <div className="createJourneyCreateNumBox">
                            <div className="createJourneyCreateNumBoxMinus" onClick={() => this.reduceDay()}>-</div>
                            <span className="createJourneyCreateNumBoxNumber">{this.state.day}</span>
                            <div className="createJourneyCreateNumBoxPlus" onClick={() => this.addDay()}>+</div>
                        </div>
                    </dd>
                </dl>
                <dl>
                    <dt>出发时间：</dt>
                    <dd>
                        <div className="createJourneyCreatePicker">
                            <DatePicker
                                mode="datetime"
                                title="选择日期"
                                extra="Optional"
                                minDate={new Date()}
                                value={this.state.date}
                                onChange={date => this.setState({ date: date })}
                            >
                                <List.Item arrow="horizontal"></List.Item>
                            </DatePicker>
                        </div>
                    </dd>
                </dl>
            </div>
        );
    }
    renderTheme() {
        if (!this.props.mdTypes || this.props.mdTypes.length <= 0)
        {
            return null;
        }
        let titImg = require('../../assets/images/journey/bg_step_tit.png');
        return (
            <div className='createJourneyTheme'>
                <div className='createJourneyThemeTit' style={{ backgroundImage: `url(${titImg})` }}>
                    02. 行程主题
                </div>
                <div className="createJourneyThemeSelect" >
                    {
                        this.props.mdTypes.map((typeItem,index) => this.renderTypesChildView(typeItem,index))
                    }
                </div>
            </div>
        );
    }
    renderTypesChildView(typeItem,index)
    {
        let iconDefault = '';
        let iconActive = '';
        if (typeItem.Type === 'JQ') {
            iconDefault = require('../../assets/images/journey/btn_scenic.png');
            iconActive = require('../../assets/images/journey/btn_scenic_active.png');
        }
        else if (typeItem.Type === 'CT') {
            iconDefault = require('../../assets/images/journey/btn_food@2x.png'); 
            iconActive = require('../../assets/images/journey/btn_food_active@2x.png');
        }
        else if (typeItem.Type === 'SQ') {
            iconDefault = require('../../assets/images/journey/btn_shopping@2x.png');
            iconActive = require('../../assets/images/journey/btn_shopping_active@2x.png');
        }
        else if (typeItem.Type === 'YL') {
            iconDefault = require('../../assets/images/journey/btn_entertainment.png');
            iconActive = require('../../assets/images/journey/btn_entertainment_active.png');
        }
        return (
            <div key={index} className="createJourneyThemeMDTypesView" onClick={() => {
                let mdTypes = [...this.props.mdTypes];
                mdTypes[index].isSelect = !typeItem.isSelect;
                this.props.dispatch(updateMDTypesAction(mdTypes));
            }} >
                {
                    typeItem.isSelect
                        ?
                        <img src={iconActive} alt='' className='createJourneyThemeSelectItemBtn' />
                        :
                        <img src={iconDefault} alt='' className='createJourneyThemeSelectItemBtn' />
                }
                <span className='createJourneyThemeSelectItemTitle'>{typeItem.Name}</span>
            </div>
        );
    }
    renderNotMiss() {
        let titImg = require('../../assets/images/journey/bg_step_tit.png');

        let checkImg = require('../../assets/images/journey/newjd.png');
        let checkImg_active = require('../../assets/images/journey/newsjd.png');

        return (
            <div className='createJourneyNotMiss'>
                <div className='createJourneyNotMissTit' style={{ backgroundImage: `url(${titImg})` }}>
                    03. 不容错过
                </div>
                <ul className='createJourneyNotMissSelect'>
                    {
                        this.props.dataList.map((item, index) =>
                            <div key={index}>
                                <div className='createJourneyNotMissSelectTitle'>
                                    {item.Name}
                                </div>
                                {
                                    item.TJtype.map((item, index) =>
                                        <li className='createJourneyNotMissSelectItem' key={index}>
                                            <div className='createJourneyNotMissSelectItemBox' onClick={() => this.changeSelect(item.Type, item.Code, item.Name)}>
                                                {
                                                    this.ifSelect(item.Code)
                                                        ?
                                                        <img src={checkImg_active} alt='' className='createJourneyNotMissSelectItemCheck' />
                                                        :
                                                        <img src={checkImg} alt='' className='createJourneyNotMissSelectItemCheck' />
                                                }
                                                <span className='createJourneyNotMissSelectItemName'>{item.Name}</span>
                                            </div>
                                        </li>
                                    )
                                }
                            </div>
                        )
                    }
                </ul>
            </div>
        );
    }
    renderCommit() {
        return (
            <div className='createJourneyCommit'>
                <div className='createJourneyCommitBtn' onClick={() => this.commit()}>
                    一键定制
                </div>
            </div>
        );
    }
    //景点是否被选中
    ifSelect(code) {
        let array = this.state.selectArray;
        for (let i = 0; i < array.length; i++) {
            if (array[i].code === code) {
                return true;
            }
        }
        return false;
    }
    //切换选中状态
    changeSelect(type, code, cnName) {
        let array = [...this.state.selectArray];
        if (array.length === 0) {
            let obj = {
                type: type,
                code: code,
                cnName: cnName
            };
            array.push(obj);
        } else {
            for (let i = 0; i < array.length; i++) {
                if (array[i].code === code) {
                    array.splice(i, 1);
                    break;
                }
                if (i === array.length - 1) {
                    array.push({
                        type: type,
                        code: code,
                        cnName: cnName
                    });
                    break;
                }
            }
        }
        this.setState({
            selectArray: array
        });
    }
    //天数减
    reduceDay() {
        if (this.state.day === 1) {
            return;
        }
        this.setState({
            day: this.state.day - 1
        });
    }
    //天数加
    addDay() {
        if (this.state.day === this.props.systemInfo.journeyMaxDayCount) {
            Modal.alert('提示', '行程天数最多可选' + this.props.systemInfo.journeyMaxDayCount + '天!' , [
                { text: 'OK', style: 'default', onPress: () => {} },
            ]);
            return;
        }
        this.setState({
            day: this.state.day + 1
        });
    }
    commit() {
        let StrJson = {};
        let planName = this.state.planName && this.state.planName.length > 0 ? this.state.planName : (this.props.userInfo.Nickname + '的行程计划');
        //行程名称，计划天数，计划日期
        let AP = {};
        AP.dates = this.state.day;
        AP.yxj = '1';
        AP.cometime = this.state.date.formatDate('YYYY-MM-DD HH:mm:ss');
        AP.sjap = '0';
        AP.xfjb = '0';
        AP.area = '1';

        // 是否在禹州境内
        let Origin = this.props.systemInfo && this.isLocationValid(this.props.systemInfo) && this.isInDistrictMap(this.props.systemInfo) ? {
            Latitude: this.props.systemInfo.latitude,
            Longitude: this.props.systemInfo.longitude,
            Address: this.props.systemInfo.address,
        } : {
            Latitude: '0',
            Longitude: '0',
            Address: '',
        };

        let TJ = {};
        TJ = this.CheckOption();

        let MD = {};
        for (let i = 0; i < this.props.mdTypes.length; i++)
        {
            let mdOption = this.props.mdTypes[i];
            if (mdOption.isSelect && mdOption.Code)
            {
                let code = mdOption.Code;
                let kv = code.split('_');
                MD[kv[0]] = code;
            }
        }

        StrJson.AP = AP;
        StrJson.Origin = Origin;
        StrJson.MD = MD;
        if (TJ.toString() !== '') {
            StrJson.TJ = TJ;
        }

        StrJson.userId = this.props.userInfo.ID;
        StrJson.planName = planName;

        let kvDict = {
            jsonParm: JSON.stringify({
                StrJson: StrJson,
                cometime: this.state.date.formatDate('YYYY-MM-DD HH:mm:ss'),
                planName: planName,
                userId: this.props.userInfo.ID
            }),
        };
        this.setState({ isLoading: true });
        HttpClient.postToPortal(IConstant.PortalApis.JOURNEY.TourPlanSmartAndSave, kvDict, null, true)
            .then((resp) => resp.json())
            .then((resp) => {
                this.setState({ isLoading: false });
                if (resp && resp.status && parseInt(resp.status,10) === 0 && resp.results && resp.results.length > 0) {
                    Modal.alert('提示', '发布成功', [
                        { text: 'OK', style: 'default', onPress: () => this.showDetail(resp.results[0].PlanMainID) },
                    ]);
                } else {
                    Modal.alert('提示', '发布失败', [
                        { text: 'OK', style: 'default' },
                    ]);
                }
            });
    }
    showDetail(code){
        this.props.dispatch(_setRefreshAction(true));
        this.props.history.push(`/journey/detail/${code}`);
    }
    CheckOption() {
        let TJ = {};
        let JQ = [];
        let YL = [];
        let CT = [];
        let SQ = [];

        let array = this.state.selectArray;
        for (let i = 0; i < array.length; i++) {
            if (array[i].type === 'JQ') {
                JQ.push({
                    Code: array[i].code,
                    Name: array[i].cnName
                });
            } else if (array[i].type === 'YL') {
                YL.push({
                    Code: array[i].code,
                    Name: array[i].cnName
                });
            } else if (array[i].type === 'CT') {
                CT.push({
                    Code: array[i].code,
                    Name: array[i].cnName
                });
            } else if (array[i].type === 'SQ') {
                SQ.push({
                    Code: array[i].code,
                    Name: array[i].cnName
                });
            }
        }
        if (YL.length > 0) {
            TJ.YL = YL;
        }
        if (JQ.length > 0) {
            TJ.JQ = JQ;
        }
        if (CT.length > 0) {
            TJ.CT = CT;
        }if (JQ.length > 0) {
            TJ.SQ = SQ;
        }
        return TJ;
    }
    onChangeName(event) {
        this.setState({
            planName: event.target.value
        });
    }
    isLocationValid(systemInfo)
    {
        return systemInfo.latitude && systemInfo.longitude && systemInfo.latitude > 0 && systemInfo.longitude > 0;
    }
    isInDistrictMap(systemInfo)
    {
        return (systemInfo.address && systemInfo.address.length > 0 ? systemInfo.address.indexOf(Config.instance.appCityName) >= 0 : false);
    }
}

const mapStateToProps = function (state, ownProps) {
    return {
        userInfo: SysUtil.getUserInfo(state),
        systemInfo: state.system,
        ...state.journey.createScreen
    };
};

CreateJourneyScreen = connect(mapStateToProps)(CreateJourneyScreen);

export default CreateJourneyScreen;


