import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';

export function asyncGetCreateAction()
{
    return function(dispatch, getState)
    {
        dispatch(_getCreateRequestAction());
        return _fetchCreate().then((resp)=>{
            for (let i = 0 ; i < resp.results.length; i++)
            {
                let group = resp.results[i];
                if (group.Type === 'CT') {
                    group.Name = '尽享美食';
                    // group.Icon = require('../../assets/images/ico_food.png');
                }
                else if (group.Type === 'JQ') {
                    group.Name =  '游山玩水';
                    // group.Icon = require('../../assets/images/ico_scenic.png');
                }
                else if (group.Type === 'SQ') {
                    group.Name =  '购物血拼';
                    // group.Icon = require('../../assets/images/ico_scenic.png');
                }
                else if (group.Type === 'YL') {
                    group.Name =  '休闲娱乐';
                    // group.Icon = require('../../assets/images/ico_entertainment.png');
                }
            }
            dispatch(_getCreateSuccessAction(resp.results));
        }).catch((error)=>{
            dispatch(_getCreateFailureAction(error));
        });
    };
}

export function asyncGetMDTypesAction()
{
    return function(dispatch, getState)
    {
        dispatch(_getMDTypesRequestAction());
        return _fetchMDTypes().then((resp)=>{
            dispatch(_getMDTypesSuccessAction(resp.results));
        }).catch((error)=>{
            dispatch(_getMDTypesFailureAction(error));
        });
    };
}

export function updateMDTypesAction(mdTypes)
{
    return {
        type: IConstant.Actions.JOURNEY.CREATE.UPDATE_MDTYPES,
        mdTypes
    };
}

export function goBackAction()
{
    return {
        type: IConstant.Actions.JOURNEY.CREATE.GO_BACK,
    };
}

function _fetchCreate()
{
    return HttpClient.getFromPortal(IConstant.PortalApis.JOURNEY.GetTjTypes)
        .then((resp) => resp.json());
}

function _getCreateRequestAction()
{
    return {
        type: IConstant.Actions.JOURNEY.CREATE.GET_CREATE_REQUEST,
    };
}

function _getCreateSuccessAction(resp)
{
    return {
        type: IConstant.Actions.JOURNEY.CREATE.GET_CREATE_SUCCESS,
        resp,
    };
}

function _getCreateFailureAction(error)
{
    return {
        type: IConstant.Actions.JOURNEY.CREATE.GET_CREATE_FAILURE,
        message: error
    };
}

function _fetchMDTypes()
{
    return HttpClient.getFromPortal(IConstant.PortalApis.JOURNEY.GetMDTypesIncKid)
        .then((resp) => resp.json());
}

function _getMDTypesRequestAction()
{
    return {
        type: IConstant.Actions.JOURNEY.CREATE.GET_MDTYPES_REQUEST,
    };
}

function _getMDTypesSuccessAction(resp)
{
    return {
        type: IConstant.Actions.JOURNEY.CREATE.GET_MDTYPES_SUCCESS,
        resp,
    };
}

function _getMDTypesFailureAction(error)
{
    return {
        type: IConstant.Actions.JOURNEY.CREATE.GET_MDTYPES_FAILURE,
        message: error
    };
}


